import { ApolloProvider } from "react-apollo";
import { HttpLink } from 'apollo-link-http'
import { ApolloClient } from 'apollo-client';
import { InMemoryCache } from 'apollo-cache-inmemory';
import App from '../components/Caching/clientQueryApp';
import ClientWriteData from '../components/Caching/ClientWriteData';
import ResetStore from '../components/Caching/onResetStore';
import ResolverUpdatePost from '../components/Caching/ResolverUpdatePost';
import Header from '../components/Header';


const cache = new InMemoryCache();
const id = "cjx2p2kbb05vx0166nurov7eh"
const votes = 444;

const link = new HttpLink({
    uri: 'https://api.graph.cool/simple/v1/cixmkt2ul01q00122mksg82pn', // Server URL (must be absolute)
    opts: {
      credentials: 'same-origin' // Additional fetch() options like `credentials` or `headers`
    }
});

const resolvers = {
    Mutation: {
      updatePost: (_root, variables, { cache, getCacheKey }) => {
        const id = getCacheKey({ __typename: 'TodoItem', id: variables.id })
        const fragment = gql`
          fragment completeTodo on TodoItem {
            completed
          }
        `;
        const todo = cache.readFragment({ fragment, id });
        const data = { ...todo, completed: !todo.completed };
        cache.writeData({ id, data });
        return null;
      },
    }
}

const client = new ApolloClient({
  cache,
  resolvers,
  link
});

const data = {
    todos: [],
    visibilityFilter: 'SHOW_ALL',
    networkStatus: {
        __typename: 'NetworkStatus',
        isConnected: false,
    },
};

cache.writeData({ data });

const Caching = () => (
    <ApolloProvider client={client}>
        <Header/>
        1. <App />
        2. <ClientWriteData/> <br/><br/>
        3. <ResetStore/> <br/><br/>
        4. <ResolverUpdatePost id={id} votes={votes}/>
    </ApolloProvider>
)
export default Caching;